import request from "@/libs/axios";
export function getList(params) {
  return request({
    url: "/dept",
    params: params
  });
}
export function getTree(params) {
  return request({
    url: "/dept/tree/get",
    params: params
  });
}
export function getInfo(id) {
  return request({
    url: "/dept/" + id
  });
}
export function create(data) {
  return request({
    url: "/dept",
    method: "post",
    data: data
  });
}
export function update(id, data) {
  return request({
    url: "/dept/" + id,
    method: "put",
    data: data
  });
}
export function remove(id) {
  return request({
    url: "/dept/" + id,
    method: "delete"
  });
}